.custom-input {
  margin: 4px 6px !important;
  border-radius: 15px !important;
  border: 1px #0c8af8 solid !important;
  padding: 4px 12px !important;
  background-color: white !important;
  height: 22px !important;
  box-shadow: 0 0 2px #0074d9 !important;
}

.custom-input:focus {
  outline: none !important;
  border: 1px solid #0c8af8 !important;
  box-shadow: 0 0 10px 2px #0074d9 !important;
}
